<template>
  <v-container>
    <v-row>
      <v-col cols=12>
       <h1 class="display-2 font-weight-bold mt-2 text-center mb-3">GITHUB USER DASHBOARD</h1>
      </v-col>
      <v-col cols=12>
         <v-text-field
         v-model='user'
        hide-details
        prepend-icon="mdi-magnify"
        single-line
        full-width
        prefix="https://api.github.com/users/"
      ></v-text-field>
      <v-alert
      v-if='alert.active'
      dense
      text
      :type="alert.type"
    >
      {{alert.msg}}
    </v-alert>
      </v-col>
      <v-col cols=12>
        <v-btn small class='pa-0 ma-0' color='primary' @click='searchOn' block>Search</v-btn>
      </v-col>
      <v-col cols=4>
        <v-card v-if='result' class='text-center'>
          <v-card-text>
            <h5>Name<br></h5>
            <h2>{{result.name}}</h2>
          </v-card-text>
        </v-card>
        <v-card v-if='result'>
            <v-row>
                <v-col cols=6>
                    <v-avatar class='ml-4' tile size=110>
                        <v-img :src='result.avatar_url'></v-img>
                    </v-avatar>
                </v-col>
                <v-col cols=5 class='mr-2'>
                    <h4 class='subtitle-1'>Public Repos: {{result.public_repos}}</h4>
                    <v-divider></v-divider>
                    <h4 class='subtitle-1'>Public Gists: {{result.public_gists}}</h4>
                    <v-divider></v-divider>
                    <h4 class='subtitle-1'>Last Update: {{result.updated_at}}</h4>
                </v-col>
            </v-row>
        </v-card>
        <v-card v-if='result' class='text-center'>
          <v-card-text>
            <h5>Followers</h5>
            <h2>{{result.followers}}</h2>
          </v-card-text>
        </v-card>
         <v-card v-if='result' class='text-center'>
          <v-card-text>
            <h5>Joined At</h5>
            <h2>{{result.created_at}}</h2>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols=8>
          <v-card>
            <div id="repoChart">
                <apexchart type="line" height="325" :options="repoChartOptions" :series="repoCountSeries"></apexchart>
            </div>
          </v-card>
      </v-col>
      <v-col cols=4>
          <v-card>
              <div id="langChart">
                <apexchart type="polarArea" width="380" :options="langChartOptions" :series="langCount"></apexchart>
            </div>
          </v-card>
      </v-col>
      <v-col cols=8>
          <v-card>
            <div id="chart">
                <apexchart type="treemap" height="380" :options="eventChartOptions" :series="eventCountSeries"></apexchart>
            </div>
          </v-card>
      </v-col>
      <v-col cols=12 class="mt-8">
       <h5>Data Source: <a href='https://docs.github.com/en/rest' target='/blank'>Github Rest API</a> | Made From: <a href='https://vuejs.org/' target='_blank'>Vue JS</a> and <a href='https://apexcharts.com/' target='_blank'>ApexChart</a></h5> 
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// import top10bar from '../top10bar'
// import heatmaplanguages from '../heatMapLanguages.vue'
// import repochart from '../repochart.vue'
import VueApexCharts from 'vue-apexcharts'
import axios from 'axios' 
  export default {
    name: 'HelloWorld',
    components:{
    //   top10bar,heatmaplanguages,repochart
    apexchart: VueApexCharts,
    },
    data: () => ({
        user:'',
        result:{},
        eventCount:[],eventCountSeries:[{data:[]}],
        langCount:[],
        repoCount:[],repoCountSeries:[{name:'Repositories',data:[]}],
        alert:{active:false,type:'',msg:''},
        // Data Viz Options
        // 1. Treemap
        eventChartOptions: {
            legend: {show: true},
            chart: {height: 350,type: 'treemap'},
            title: {text: 'Events By Size',align: 'center'},
            dataLabels: {enabled: true,style: {fontSize: '12px',},formatter: function(text, op) {return [text, op.value]},offsetY: -4},
            plotOptions: {treemap: {enableShades: true,shadeIntensity: 0.5,reverseNegativeShade: true,
                colorScale: {
                  ranges: [
                    {from: -6,to: 0,color: '#CD363A'},
                    {from: 0.001,to: 6,color: '#52B12C'}
                  ]
                }
              }
            }
          },
        // 2. Polar Graph
        langChartOptions: {
            chart: {width: 380,type: 'polarArea'},
            labels:[],
            title: {text: 'Languages Frequently Used',align: 'center'},
            fill: {opacity: 1},
            stroke: {width: 1,colors: undefined},
            yaxis: {show: false},
            legend: {show:true,position: 'bottom'},
            plotOptions: {polarArea: {rings: {strokeWidth: 0},spokes: {strokeWidth: 0},}},
            theme: {monochrome: {enabled: false,shadeTo: 'light',shadeIntensity: 0.6}}
          },
        // 3. Line Chart
        repoChartOptions: {
            legend: {show: true},
            chart: {height: 350,type: 'line',zoom: {enabled: false}},
            dataLabels: {enabled: true},stroke: {curve: 'straight'},
            title: {text: 'Rpositories each Year',align: 'center'},
            grid: {row: {colors: ['#f3f3f3', 'transparent'], opacity: 0.5},},
            xaxis: {categories: []}
          },
    }),
    methods:{
        clearData(){
            this.eventCountSeries[0].data=[]
            this.eventCount=[]
            this.langCount=[]
            // this.langChartOptions.labels=[]
            this.repoCount=[]
            this.repoChartOptions.xaxis.categories=[]
        },
        searchOn(){
            // axios.interceptors.request.use((config) => {
            //   this.clearData()
            //   return config;
            // })
            axios.get('https://api.github.com/users/'+this.user)
                .then(resp=>{
                    this.result=resp.data
                    this.clearData()
                    // console.log(resp)
                    this.alert.active=true
                    this.alert.msg='We got '+resp.data.login+'. Here you go!'
                    this.alert.type='success'
                    // Events
                    axios.get('https://api.github.com/users/'+this.user+'/events')
                        .then(ev=>{
                            // console.log(ev.data)
                            let loopData={}
                            ev.data.forEach(element => {
                                loopData[element.type]=(loopData[element.type] || 0)+1
                                // console.log(element)
                            })
                            Object.keys(loopData).forEach(key => {
                                this.eventCount.push({x:key,y:loopData[key]})
                            })
                            this.eventCountSeries=[{data:this.eventCount}]
                        })
                    // this.eventCountSeries[0].data=this.eventCount
                    // Repos and Languages
                    axios.get('https://api.github.com/users/'+this.user+'/repos')
                        .then(repo=>{
                            let loopData={}
                            let repoData={}
                            repo.data.forEach(element => {
                                let repoyear=element.created_at.substr(0,4)
                                loopData[element.language]=(loopData[element.language] || 0)+1
                                repoData[repoyear]=(repoData[repoyear] || 0)+1
                            })
                            Object.keys(loopData).forEach((key,index) => {
                                this.langCount.push(loopData[key])
                                this.langChartOptions.labels[index]=key
                            })
                            Object.keys(repoData).forEach(key => {
                                this.repoCount.push({x:key,y:repoData[key]})
                                this.repoChartOptions.xaxis.categories.push(key)
                            })
                            this.repoCountSeries=[{data:this.repoCount}]
                        })
                    // this.repoCountSeries[0].data=this.repoCount
                    // console.log(this.repoChartOptions.xaxis.categories[2])
                    console.log(this.langChartOptions.labels)
                })
                .catch((err)=>{
                    // this.clearData()
                    this.alert.active=true
                    this.alert.msg=err.message
                    this.alert.type='error'
                    this.result={}
                    console.log(err)
                })
                .finally(()=>{

                    setTimeout(() => {
                        this.alert.active=false
                        this.alert.msg=''
                        this.alert.type=''
                    }, 3000);
                })
        }
    }
  }
</script>
